.main-window {
      position: relative;
      top: 15%;
      left: 0;
      width: 100%;
      height: 65%;
}

.center-window {
      position: absolute;
      height: 100%;
      width: 70%;
      top: 0;
      left: 15%;
      
      display: flex;
      align-items: center;
      justify-content: center;
      transition: all 0.6s;
      background-color: rgb(203, 203, 254);
      border: 2px ridge #0e042d;
      box-shadow: 3px 3px 7px 3px rgb(0 0 0 / 0.4);
}
.left-window {
      position: absolute;
      height: 60%;
      width: 60%;
      top: 20%;
      left: 6%;

      display: flex;
      align-items: center;
      justify-content: center;
      transition: all 0.6s;
      background-color: rgb(238, 238, 242);
      border: 2px ridge #0e042d;;
      box-shadow: 3px 3px 7px 3px rgb(0 0 0 / 0.2);
}
.right-window {
      position: absolute;
      height: 60%;
      width: 60%;
      top: 20%;
      left: 34%;

      display: flex;
      align-items: center;
      justify-content: center;
      transition: all 0.6s;
      background-color: rgb(238, 238, 242);
      border: 2px ridge #0e042d;
      box-shadow: 3px 3px 7px 3px rgb(0 0 0 / 0.4);
}
.mmf-text-wrapper {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      font-size: 18px;
      color: #0e042d;
      justify-content: left;
      padding-left: 5%;
      padding-right: 5%;
      position: relative;
      border-left: 2px solid #0e042d;
      border-radius: 4px;
      height: 80%;
}
.mmf-text {
      font-size: 1em;
      text-align: left;
      padding-top: 5%;
      padding-bottom: 5%;
      border-top: 1px solid #0e042d;
      /* border-bottom: 1px solid #0e042d; */
      text-overflow: ellipsis;
      overflow: hidden;
}
.mmf-text-header {
      font-family: Poppins, sans-serif;
      font-size: 2em;
      text-align: left;
}
.mmf-media {
      display: flex;
      flex-direction: column;
      position: relative;
      width: 50%;
      height: 80%;
      padding-left: 5%;
      padding-right: 5%;
      border-right: 2px solid #0e042d;
      border-radius: 4px;
}
.mmf-iframe {
      aspect-ratio: 1/1;
      width: 100%;
      /* transform: scaleY(130%); */
}
.submit-name-wrapper {
      position: relative;
      text-align: left;
      margin-top: auto;
}
.submit-text {
      font-size: 2em;
}
.submit-name {
      font-size: 1.5em;
}

.left-arrow-wrapper {
      position: absolute;
      width: 3%;
      height: 10rem;
      top: 45%;
      left: 1%;

      transition: all 0.2s;
}
.right-arrow-wrapper {
      position: absolute;
      width: 3%;
      height: 10rem;
      top: 45%;
      left: 96%;
      transition: all 0.2s;
}

.right-arrow-wrapper:hover {
      transition: all 0.2s;
      transform: scaleX(160%);
      transform: scaleY(120%);
}

.left-arrow-wrapper:hover {
      transition: all 0.2s;
      transform: scaleX(160%);
      transform: scaleY(120%);
}
.left-arrow-wrapper:active {
      transition: all 0.1s;
      transform: scaleX(100%);
      transform: scaleY(100%);
}
.right-arrow-wrapper:active {
      transition: all 0.1s;
      transform: scaleX(100%);
      transform: scaleY(100%);
}
.mmf-arrow {
      height: 100%;
      width: 100%;
      object-fit: fill;
}