.multimedia-feed-container {
      display: flex;
      width: 100%;
      height: 100vh;
      background-color: white;
      border-top: 6px ridge #0e042d;
      border-bottom: 6px ridge #0e042d;
      position: relative;
}

.multimedia-feed-button {
      height: 3rem;
      width: 20rem;
      background-color: #32006A;
      position: absolute;
      top: 89%;
      left: 15%;
      border-radius: 15px;
      transition: all 0.3s linear;
      color: white;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 1.3rem;
      border: 4px solid #0e042d;
      box-shadow: 3px 3px 7px 3px rgb(0 0 0 / 0.4);
}
.multimedia-feed-button:hover {
      transition: all 0.3s linear;
      transform: scale(110%);
}

.multimedia-feed-navbar {
      position: absolute;
      height: 3.5rem;
      width: 74%;
      border-color: gray;
      top: 6%;
      left: 13%;
      border-top:  4px solid #0e042d;
      border-bottom:  4px solid #0e042d;
      box-shadow: 0 -5px 3px -3px rgb(0 0 0 / 0.4), 0 5px 3px -3px rgb(0 0 0 / 0.4);
      border-radius: 3px;
}